@import "colors";


.customPagination__main {
    margin: 50px 0;

}


.customPagination__main ul {
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
}

.customPagination__main>ul>li {
    padding: 4px 10px;
    border: 1px solid lightgray;
    cursor: pointer;
    font-size: 16px;

    &.selected {
        color: #000;
        font-weight: 700;
        border: 1px solid #000;
        background-color: lightgray;
    }
}