@import "colors";


.allJobs__main {
    .jobFilerSection {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        >select {
            border: 1px solid $lightblack;
            outline: none;
            font-size: 12px;
            padding: 5px;
            width: min-content;
            margin: 5px;
            border-radius: 50px;
            color: $lightblack;
            cursor: pointer;

            >option {
                background-color: $lightblack;
                color: #fff;
                padding: 5px !important;
            }

            >option:active,
            >option:checked,
            >option:hover {
                background-color: $black !important;
            }

            @media (max-width: 450px) {
                width: 130px;
            }
        }
    }

    .jobCard {
        border: 1px solid lightgray;
        padding: 7px 15px;
        border-radius: 5px;
        margin-top: 15px;

        &:hover {
            box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.3);
        }

        >h3 {
            font-size: 16px;
            font-weight: 700;
            margin: 0;
            padding: 0;
            color: black;
        }

        >span {
            color: $black;
            font-size: 14px;
            display: block;
            margin-bottom: 5px;
        }

        >p {
            display: flex;
            align-items: center;
            margin-bottom: 5px;
            font-size: 14px;

            >svg {
                margin-right: 5px;
                font-size: 18px;
                color: $lightblack;
            }
        }

        >.date__btn {
            display: flex;
            align-items: center;
            justify-content: space-between;

            >p {
                font-size: 14px;
            }

            >a {
                font-size: 14px;
                color: $blue;

                &:hover {
                    color: $green;
                }
            }
        }
    }


    .customPagination__main {
        margin: 0 0 100px 0;
    }
}